.App {
  background: "white";
  flex: 1;
  align-content: "center";
  justify-content: "center";
  align-items: "center";
}

.headerImage {
  align-self: left;
  margin-left: 50px;
}

.frame {
  width: 100%;
  height: 100%;
}

.frameContainer {
  width: 100%;
  height: 900px;
  padding: 30px;
}

.options {
  float: right;
  margin-top: 40px;
  margin-right: 55px;
  color: gray;
}

.selected {
  color: red;
}

.status {
  margin: 0 auto;
  text-align: center;
  margin-top: 145px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.headerTitle {
  text-align: "center";
}
.icon {
  padding-left: 50px;
  padding-top: 3px;
}
.header {
  background: white;
  height: 75px;
  /*border-bottom: 2px solid lightgrey;*/
  box-shadow: 0 8px 6px -6px black;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
